.side-nav{
	padding:30px;
	height:400px;
	background:#95ACA8;
	box-shadow:1px 0px 7px rgba(0,0,0,0.5);
	position:fixed;
	top:0;
	right:0;
	width:70%;
	max-width:400px;
	width:350px;
	z-index:200;
	transition: transform 0.5s, ease-out;
	border-bottom-left-radius: 400px;
}

.hide{
	transform:translateX(+100%);
}

.bar{
	position:fixed;
	z-index:200;
	top:10px;
	right:10px;
}



